<template>
  <div class="container-fluid">
    <div class="row">
      <main role="main" class="col-md-12 ml-sm-auto col-lg-12 pt-3 px-4">
        <div
          class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pb-2 mb-3"
        >
          <h2>VueSheet List</h2>
          <div class="btn-toolbar mb-2 mb-md-0">
            <a
              :href="gs_api"
              class="btn btn-sm btn-outline-secondary"
              target="_blank"
            >
              View Google Sheet
            </a>
          </div>
        </div>
        <div class="table-responsive">
          <table class="table table-striped">
            <thead>
              <tr>
                <th>Name</th>
                <th>Phone</th>
                <th>品項</th>
                <th>甜度</th>
                <th>冰塊</th>
                <th>備註</th>
              </tr>
            </thead>

            <tbody>
              <Row v-bind:key="row.id" v-for="row in rows" v-bind:row="row" />
            </tbody>
          </table>
        </div>
      </main>
    </div>
    <div class="row">
      <div class="col-md-6 mb-3">
        <label for=""></label>
        <button class="btn btn-info btn-block" v-on:click="notifyToLine()" style="width:100px">Notify</button>
        
      </div>
      
    </div>
  </div>
</template>

<script>
import Row from "@/components/Row.vue";
const { GoogleSpreadsheet } = require("google-spreadsheet");
const creds = require("@/client_secret.json");


export default {
  name: "Sheet",
  components: {
    Row,
  },
  props: ["sheet"],
  data() {
    return {
      rows: [],
      loading: true,
      gs_api: process.env.VUE_APP_GOOGLESHEET_URL,
    };
  },
  methods: {
    async notifyToLine() {
      const token = process.env.VUE_APP_LN_TOKEN;
				const url = process.env.VUE_APP_LN_API;
				return fetch(url, {
					method: 'POST',
					headers: {
						'Content-Type':'application/x-www-form-urlencoded',
						'Authorization': `Bearer ${token}`,
					},
					data: {
						message: '鮮蝦煲冬粉',
            stickerPackageId : "1",
            stickerId :"401",
					}
				}).then(function(result) {
					console.log('SUCCESS TO LINE!', result.text);
				})
				.catch(function(error) {
					console.log('FAILED TO LINE...', error.text);
				});

      /*var obj = {
        message: "GG",
        stickerPackageId : "1",
        stickerId :"401",
      };
      fetch("https://script.google.com/macros/s/AKfycbx0uVJM7jNOdufmMk4hIGM5ciqaaLyU352X5IQNvJ0cko2Ni5wpyZOcFqoiWQxlr2FV/exec", {
          redirect: "follow",
          method: "POST",
          headers: {
            "Content-Type": "text/plain;charset=utf-8",
          },
          body: JSON.stringify(obj),
      }).then(function(result) {
					console.log('SUCCESS!', result.toString());
				})
				.catch(function(error) {
					console.log('FAILED...', error.toString());
				});*/


    },
    oAuth2() {
            var URL = 'https://notify-bot.line.me/oauth/authorize?';
            URL += 'response_type=code';
            URL += '&client_id=hdgN7YbRU44ZcyDRDyDtEx';
            URL += '&redirect_uri=http://localhost:8080';
            URL += '&scope=notify';
            URL += '&state=NO_STATE';
            window.location.href = URL;
    },
    async accessSpreadSheet() {
      const doc = new GoogleSpreadsheet(process.env.VUE_APP_GOOGLESHEET_TOKEN);
      await doc.useServiceAccountAuth(creds);
      await doc.loadInfo();
      const sheet = doc.sheetsByIndex[0];
      const rows = await sheet.getRows({
        offset: 0,
      });
      this.rows = rows;
    },
  },
  created() {
    this.accessSpreadSheet();
  },
};
</script>

