<template>
  <div class="container-fluid">
    <div class="row">
      <main role="main" class="col-md-12 ml-sm-auto col-lg-12 pt-3 px-4">
        <div class="container">
          <div
            class="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pb-2 mt-3"
          >
            <h4 class="mb-3">New row</h4>
            <div class="btn-toolbar mb-2 mb-md-0">
              <router-link to="/" class="btn btn-sm mr-1 btn-outline-secondary">
                Back
              </router-link>
              <a
                v-bind:href="gs_api"
                class="btn btn-sm btn-outline-secondary"
                target="_blank"
              >
                View Google Sheet
              </a>
            </div>
          </div>
          <div
            v-if="showMsg"
            class="alert alert-success alert-dismissible fade show"
            role="alert"
          >
            {{ message }}
            <button
              type="button"
              class="close"
              data-dismiss="alert"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="col-md-12 order-md-1">
            <form @submit.prevent="addRow">
              <div class="row">
                <div class="col-md-6 mb-3">
                  <label for="emp_name">姓名(工號)</label>
                  <input type="text" v-model="emp_name" class="form-control" id="emp_name" placeholder="Tom Lin(500100)" required/>
                </div>
                <div class="col-md-6 mb-3">
                  <label for="emp_phone">電話</label>
                  <input type="text" v-model="emp_phone" class="form-control" id="emp_phone" placeholder="#12345" required/>
                </div>
                <div class="col-md-12 mb-3">
                  <label for="order">品項</label>
                  <textarea v-model="order" class="form-control" id="order" rows="2" max-rows="6" placeholder="草莓紅茶" required></textarea>
                </div>

                <div class="col-md-6 mb-3">
                  <label for="activity">甜度</label>
                  <select v-model="sweetness_level" class="custom-select d-block w-100" id="sweetness_level" required>
                    <option value="">Choose...</option>
                    <option value="正常糖">正常糖</option>
                    <option value="少糖">少糖</option>
                    <option value="半糖">半糖</option>
                    <option value="微糖">微糖</option>
                    <option value="無糖">無糖</option>
                  </select>
                </div>
                <div class="col-md-6 mb-3"></div>
                <div class="col-md-6 mb-3">
                  <label for="amount_of_ice">冰塊</label>
                  <select v-model="amount_of_ice" class="custom-select d-block w-100" id="amount_of_ice" required>
                    <option value="">Choose...</option>
                    <option value="正常">正常</option>
                    <option value="少冰">少冰</option>
                    <option value="去冰">去冰</option>
                  </select>
                </div>
                <div class="col-md-6 mb-3"></div>
                <div class="col-md-6 mb-3">
                  <label for="note">備註</label>
                  <textarea v-model="note" class="form-control" id="note" rows="2" max-rows="6" placeholder="ex: 加珍珠" required></textarea>
                </div>
                <div class="col-md-6 mb-3"></div>
                <div class="col-md-6">
                  <label for=""></label>
                  <button class="btn btn-primary btn-block" type="submit"> SUBMIT </button>
                  <button class="btn btn-info btn-block" v-on:click="notifyToLine()" style="width:100px">Notify</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
const { GoogleSpreadsheet } = require("google-spreadsheet");
const creds = require("@/client_secret.json");
//const request = require("request");
import emailjs from "@emailjs/browser";
import $ from "jquery";

export default {
  name: "AddRow",
  data() {
    return {
      emp_name: "",
      emp_phone: "",
      order: "",
      sweetness_level: "",
      amount_of_ice: "",
      note: "",
      showMsg: false,
      message: "",
      gs_api: process.env.VUE_APP_GOOGLESHEET_URL,
    };
  },
  methods: {
    async addRow() {
      const newRow = {
        emp_name: this.emp_name,
        emp_phone: this.emp_phone,
        order: this.order,
        sweetness_level: this.sweetness_level,
        amount_of_ice: this.amount_of_ice,
        note: this.note,
      };

      const doc = new GoogleSpreadsheet(process.env.VUE_APP_GOOGLESHEET_TOKEN);
      await doc.useServiceAccountAuth(creds);
      await doc.loadInfo();
      const sheet = doc.sheetsByIndex[0];
      await sheet.addRow(newRow);
      this.emp_name = "";
      this.emp_phone = "";
      this.order = "";
      this.sweetness_level = "";
      this.amount_of_ice = "";
      this.note = "";
      this.message = "New row added !";
      this.showMsg = true;
      //this.sendEmail();
    },
    sendEmail() {
      var templateParams = {
        to_name: this.name,
        reply_to: "sophia4619@gmail.com",
        message: "Check this out!",
      };
      emailjs
        .send(
          process.env.VUE_APP_EMAIL_SERVICE_ID,
          process.env.VUE_APP_EMAIL_TEMPLATE_ID,
          templateParams,
          process.env.VUE_APP_EMAIL_PUBLIC_KEY
        )
        .then(
          (result) => {
            console.log("SUCCESS!", result.text);
          },
          (error) => {
            console.log("FAILED...", error.text);
          }
        );
    },
    sendEmailAPI() {
      //use REST api
      var data = {
        service_id: process.env.VUE_APP_EMAIL_SERVICE_ID,
        template_id: process.env.VUE_APP_EMAIL_TEMPLATE_ID,
        user_id: process.env.VUE_APP_EMAIL_PUBLIC_KEY,
        template_params: {
          to_name: this.name,
          reply_to: "sophia4619@gmail.com",
          message: "Check this out!",
        },
      };

      $.ajax(process.env.VUE_APP_EMAIL_API, {
        type: "POST",
        data: JSON.stringify(data),
        contentType: "application/json",
      })
        .done(function () {
          alert("Your mail is sent!");
        })
        .fail(function (error) {
          alert("Oops... " + JSON.stringify(error));
        });
    },

    notifyToLine() {
      const token = process.env.VUE_APP_LN_TOKEN;
				const url = process.env.VUE_APP_LN_API;
				return fetch(url, {
					method: 'POST',
					headers: {
						'Content-Type':'application/x-www-form-urlencoded',
						'Authorization': `Bearer ${token}`,
					},
					data: {
						"message": '鮮蝦煲冬粉'
					}
				}).then(function(result) {
					console.log('SUCCESS TO LINE!', result.text);
				})
				.catch(function(error) {
					console.log('FAILED TO LINE...', error.text);
				});
    },
  },
};
</script>

<style scoped></style>
